@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
input[type='text'] {
  border: none;
  border-bottom: 1px solid #d9d9d9; }

.dropdown-toggle::after {
  transition: 0.3s all; }

.show .dropdown-toggle::after {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg); }

.dropdown-menu {
  width: 100%; }

.dropdown-button {
  width: 100%;
  background: #02313b;
  border-radius: 0 0 3px 3px;
  text-align: center; }
  .dropdown-button button {
    color: #02313b;
    background: #f4f4f4; }
    .dropdown-button button:hover {
      color: #b99379; }

.beds-buttons {
  border: 1px solid grey;
  transition: 0.23s;
  cursor: pointer; }
  .beds-buttons:hover {
    background: #b99379;
    color: white; }

.beds-active {
  background: #b99379;
  color: white; }

.beds-buttons:nth-of-type(1) {
  border-radius: 5px 0 0 5px; }

.beds-buttons:nth-last-of-type(1) {
  border-radius: 0 5px 5px 0; }

.search-clear {
  position: absolute;
  right: 10px;
  top: 6px;
  cursor: pointer;
  background: white; }

@media only screen and (min-width: 768px) {
  .move-left {
    left: calc(-140px + 30%); } }

.list-of-houses {
  height: calc(100vh - 46px);
  margin: 0px;
  overflow: none; }

.h-90 {
  height: 90%; }

.top-0 {
  top: 0; }

.bg-yellow {
  background-color: #DEB887;
  padding: 0px 5px;
  margin: 5px; }

.border-radius-10 {
  border-radius: 10px; }

.face-img {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%; }

.no-user-selection {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none; }

.text-10 {
  font-size: 11px; }

#house-details {
  width: 100%;
  z-index: 2;
  display: grid;
  grid-template-areas: 'house-username house-username house-price' 'house-address house-beds house-surface';
  font-size: 12px;
  align-items: center; }
  #house-details * {
    margin: 0 5px; }
  #house-details #house-username {
    grid-area: house-username;
    font-size: 14px; }
  #house-details #house-address {
    grid-area: house-address; }
  #house-details #house-surface {
    grid-area: house-surface; }
  #house-details #house-beds {
    grid-area: house-beds; }
  #house-details #house-price {
    grid-area: house-price;
    font-size: 14px; }

#black-layer-bottom {
  background: linear-gradient(transparent 0%, transparent 40%, black 90%);
  opacity: 0.45;
  z-index: 1;
  width: calc(100% - 30px);
  height: 90%; }

.actual-page {
  background-color: #4895e8;
  color: white; }
  .actual-page:hover {
    background: #4895e8;
    color: white; }

.img-icon {
  width: 1.3rem;
  height: 1.3rem; }

@media only screen and (min-width: 768px) {
  .list-of-houses {
    overflow: auto; } }

.map-size {
  width: 100%;
  height: calc(100vh - 46px); }

.flex {
  display: flex; }

.flex-center {
  flex-flow: column;
  justify-content: center; }

.house-img-size {
  width: 75px;
  height: 75px; }

.reset-padding {
  padding-left: 0px;
  padding-right: 0px; }

#divided-view {
  display: none; }

#full-view {
  display: block; }
  #full-view > img {
    position: fixed;
    bottom: 25px;
    left: 25px;
    z-index: 999;
    width: 35px;
    height: 35px;
    cursor: pointer; }

@media only screen and (min-width: 1024px) {
  #full-view {
    display: none; }
  #divided-view {
    display: grid;
    grid-template-columns: 1fr 1fr; } }

body {
  margin: 0;
  padding: 0; }

ol, ul {
  list-style: none; }

a {
  text-decoration: none; }

